  bottom: 6px;
  left: 6px;
  border: 1px solid rgba(255, 255, 255, 0.6);
}

.portfolio-items .portfolio-item .hover-bg a {
  display: block;
}

.portfolio-items .portfolio-item .hover-bg img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

@media (max-width: 673px) {
  .portfolio-items {
    width: 100%;
  }
  .portfolio-items .portfolio-item {
    width: 100%;
  }
}

.hover-bg .hover-text {
  position: absolute;
  text-align: center;
  margin: 0 auto;
  color: #fff;
  background: rgba(0, 0, 0, 0.6);
  padding: 30% 0 0 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: all 0.5s;
}

.hover-bg .hover-text > h4 {
  opacity: 0;
  color: #fff;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  transition: all 0.3s;
  font-size: 17px;
  letter-spacing: 0.5px;
  font-weight: 500;
}

.hover-bg:hover .hover-text {
  opacity: 1;
}

.hover-bg:hover .hover-text > h4 {
  opacity: 1;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

